.app-wrapper {
    width: 100%;
    margin: auto;
    height: 100%;
    display: grid;
    grid-template-areas:
            "h h"
            "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;

}

.app-wrapper-content {

    grid-area: c;
    background-color: #010409;
}